// fonts
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;300;400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');

// font-awesome
@import "~@fortawesome/fontawesome-free/scss/fontawesome.scss";
@import "~@fortawesome/fontawesome-free/scss/regular.scss";
@import "~@fortawesome/fontawesome-free/scss/solid.scss";

// overrides
@import "assets/styles/bootstrap-overrides";
@import "assets/styles/blog-styles";

// bootstrap
@import "~bootstrap/scss/bootstrap";

// primeng
@import "~primeicons/primeicons.css";
@import "~primeng/resources/primeng.css";
@import "~primeng/resources/themes/lara-light-indigo/theme.css";

* {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  font-size: 1rem !important;
}

h1 {
  color: $secondary-dark;
  line-height: 1.7;
  border-bottom: 0.25rem solid $orange;
}

h2 {
  color: $secondary-dark;
  line-height: 1.7;
  border-bottom: 0.25rem solid $warning;
}

h3 {
  color: $secondary-dark;
  line-height: 1.7;
  border-bottom: 0.25rem solid $red;
}

h4 {
  color: $secondary-dark;
  line-height: 1.7;
  border-bottom: 0.25rem solid $rose;
}

h5 {
  color: $secondary-dark;
  line-height: 1.7;
  border-bottom: 0.25rem solid $pink;
}

h6 {
  color: $secondary-dark;
  line-height: 1.7;
  border-bottom: 0.25rem solid $purple;
}

.btn-link {
  color: linear
}

.font-serif {
  font-family: 'Merriweather', serif;
}

.font-sans-serif {
  font-family: 'Ubuntu', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.cursor-pointer:hover, .cursor-pointer:focus, .cursor-pointer:active {
  cursor: pointer;
}
